.contact-form {
    width: 100%;
    margin-top: 10px;

    ul {
        padding: 0;
        margin: 0;

        li {
            padding: 0;
            margin: 0;
            list-style: none;
            margin-bottom: 10px;
            overflow: hidden;
            display: block;
            position: relative;
            opacity: 0;
            animation: fadeInUp 2s 2s;
            animation-fill-mode: forwards;
            clear: both;
        }
        li.half{
            width: 49%;
            margin-left: 2%;
            float: left;
            clear: none;

            &:first-child {
                margin-left: 0;
            }
        }
    }
    input[type='text'],
    input[type='email'] {
        width: 100%;
        border: 0;
        background: #cedce4;
        height: 50px;
        font-size: 16px;
        color: rgb(7, 7, 7);
        padding: 0 20px;
        box-sizing: border-box;
    }
    textarea{
        width: 100%;
        border: 0;
        background: #cedce4;
        height: 50px;
        font-size: 16px;
        color: rgb(7, 7, 7);
        padding: 20px;
        min-height: 150px;
        box-sizing: border-box;
    }
    .flat-button{
        color: #f1cf72;
        background: 0 0;
        font: 11px;
        letter-spacing: 3px;
        text-decoration: none;
        padding: 8px 10px;
        border: 1px solid #f1cf72;
        float: right;
        border-radius: 4px;
        animation: fadeIn 1s 1.8s backwards;
        white-space: nowrap;
        cursor: pointer;

        &:hover {
            background: #f1cf72;
            color: #333;
        }
    }

}
.info-map{
    position:absolute;
    background-color: #000;
    top: 50px;
    right: 30%;
    z-index: 999999;
    width: 267px;
    padding: 20px;
    color: #fff;
    font-family: 'Helvetica';
    font-size: 17px;
    font-weight: 300;
    opacity: 0;
    animation: fadeIn 1s 1.5s;
    animation-fill-mode: forwards;

    span {
        font-size: 16px;
        display: block;
        padding-top: 20px;
        color: #f1cf72;
    }
}

.map-wrap {
    background: rgba(8, 253, 216, 0.1);
    float: right;
    width: 53%;
    height: 100%;
}
.leaflet-container {
    width: 100%;
    height: 100%;
    position: relative;
    opacity: 0;
    animation: backInRight 1s 1.2s;
    animation-fill-mode: forwards;
    
}

@media screen and (max-width: 1200px) {
    .map-wrap{
        float: none;
        width: 80%;
        height: 320px;
        margin-left: 10%;
        clear: both;
    }
    .info-map {
        margin-left: 10%;
        width: 100px 80%;
        font-size: 13px;
        position: static;
        clear: both;
       
        
        
        span {
            font-size: 13px;
            display: block;
            padding-top: 20px;
            
        }
        
    }
    

}
